<style>
   
</style>
<template>
    <div class="home">
        <!--顶部开始-->
        <div class="index_top">
            <div class="index_top_left">
                <img src="../../assets/images/logo.png" alt="">
            </div>
            <div class="index_top_center">
                <div>{{ lang_list.home_mean_1 }}</div>
                <div @click="toGong()">{{ lang_list.home_mean_2 }}</div>
                <div style="color: #f2f2f2;">NFT</div>
            </div>
            <div class="index_top_right" @click="switchLang">
                EN/中
            </div>
            <div class="index_clear"></div>
        </div>
        <!--顶部结束-->
        <!--头部开始-->
        <div class="index-header">
            <div>
                <div class="index-header-text1">{{ lang_list.home_title }}</div>
                <br/>
                <div class="col-xs-12 col-sm-6 index-header-tab">
                    {{ lang_list.home_title_desc }} </div>
                <div style="clear: both;"></div>
                <div>
                    <div class="index-header-button" @click="toGong()">
                        {{ lang_list.home_mean_2 }}
                    </div>
                </div>

            </div>
        </div>
        <!--头部结束-->
        <!--内容开始-->
        <div class="index-content">
            <img src="../../assets/images/b.png"
                 class="index-content-img" alt="">
            <div class="index-content-1">
                <div class="index-header-text1 index-content-title">1.{{ lang_list.home_title1 }}</div>
                <div>
                    <div class="col-xs-12 col-sm-8 index-content-text" v-html="lang_list.home_desc1">
                    </div>
                    <img src="../../assets/images/1.png" alt="" class="col-xs-12 col-sm-4 index-content-img-bottom">
                    <div style="clear: both;"></div>
                </div>

            </div>
            <div class="index-content-2">
                <div class="index-header-text2 index-content-title" style="float: right;">2.{{ lang_list.home_title2 }}</div>
                <div style="clear: both;"></div>
                <div>
                    <img src="../../assets/images/2.png" alt="" class="col-xs-12 col-sm-4 index-content-img-bottom">
                    <div class="col-xs-12 col-sm-8 index-content-text" v-html="lang_list.home_desc2">

                    </div>
                    <div style="clear: both;"></div>
                </div>

            </div>
            <div class="index-content-3">
                <div class="index-header-text1 index-content-title">3.{{ lang_list.home_title3 }}</div>
                <div>
                    <div class="col-xs-12 col-sm-8 index-content-text" v-html="lang_list.home_desc3">

                    </div>
                    <img src="../../assets/images/3.png" alt="" class="col-xs-12 col-sm-4 index-content-img-bottom">
                    <div style="clear: both;"></div>
                </div>

            </div>
            <div class="index-content-4">
                <div class="index-header-text2 index-content-title" style="float: right;">4.{{ lang_list.home_title4 }}</div>
                <div style="clear: both;"></div>
                <div>
                    <img src="../../assets/images/4.png" alt="" class="col-xs-12 col-sm-4 index-content-img-bottom">
                    <div class="col-xs-12 col-sm-8 index-content-text" v-html="lang_list.home_desc4">

                    </div>
                    <div style="clear: both;"></div>
                </div>

            </div>
            <div class="index-content-5">
                <div class="index-header-text1 index-content-title">5.{{ lang_list.home_title5 }}</div>
                <div>
                    <div class="col-xs-12 col-sm-8 index-content-text" v-html="lang_list.home_desc5">
                    </div>
                    <img src="../../assets/images/5.png" alt="" class="col-xs-12 col-sm-4 index-content-img-bottom">
                    <div style="clear: both;"></div>
                </div>

            </div>
            <div class="index-content-6">
                <div class="index-header-text2 index-content-title" style="float: right;">6.{{ lang_list.home_title6 }}</div>
                <div style="clear: both;"></div>
                <div>
                    <img src="../../assets/images/6.png" alt="" class="col-xs-12 col-sm-4 index-content-img-bottom">
                    <div class="col-xs-12 col-sm-8 index-content-text" v-html="lang_list.home_desc6">

                    </div>
                    <div style="clear: both;"></div>
                </div>

            </div>
        </div>
        <div>
            <img src="../../assets/images/bottom.png" alt="" style="width: 100%;">
        </div>
        <!--内容结束-->
        <div class="loadWindow" v-if="show"></div>
        <div class="loadBox" v-if="show">
            <div class="loadTitle">{{ lang_list.home_toast }}...</div>
            <img src="../../assets/img/load.gif" style="width: 100%;"/>
        </div>
    </div>
</template>
<script>
    let provider, signer, defaultAccount, networkVersion;
    let usdtContract, usdtAddress = '0xCDAbD94A40e25E80Cd4CE1D73C8f93e368BD1069';
    let conContract, conAddress = '0x0Fd7Eb335Fc392f1C8b12D89Ce27423B0BBD5319';
    export default {
        data() {
            return {
                lang: 'zh',
                lang_list: {},
                address: '',
                short_address: '连接钱包',
                disable: false,
                show: false,
                meanShow: false,
                type: '',
                num: 1000000,
            }
        },
        created: function () {
            this.checkNet()
        },
        async mounted() {
            this.lang = sessionStorage.getItem('lang') ? sessionStorage.getItem('lang') : 'zh';
            this.lang_list = this.zh;
            if (this.lang == 'en') {
                this.short_address = 'connect';
                this.lang_list = this.en;
            }
            window.addEventListener('load', async () => {
                // Modern dapp browsers...
                if (window.ethereum) {
                    try {
                        window.web3 = new Web3(ethereum);
                        // Request account access if needed
                        await ethereum.enable();
                        // Acccounts now exposed
                        web3.eth.sendTransaction({/* ... */});
                    } catch (error) {
                        // User denied account access...
                    }
                }
                // Legacy dapp browsers...
                else if (window.web3) {
                    window.web3 = new Web3(web3.currentProvider);
                    // Acccounts always exposed
                    web3.eth.sendTransaction({/* ... */});
                }
                // Non-dapp browsers...
                else {
                    console.log('Non-Ethereum browser detected. You should consider trying MetaMask!');
                }
            });
        },
        methods: {
            connectWallet() {
                if (this.address) {
                    this.short_address = this.address.substr(0, 3) + '...' + this.address.substr(this.address.length - 3)
                } else {
                    this.checkNet()
                }
            },
            switchLang() {
                if (this.lang == 'zh') {
                    this.lang == 'en'
                    sessionStorage.setItem('lang', 'en');
                } else {
                    this.lang == 'zh'
                    sessionStorage.setItem('lang', 'zh');
                }
                location.reload();
            },
            getUrlParam(name) {
                var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
                var r = window.location.search.substr(1).match(reg);  //匹配目标参数
                if (r != null) return unescape(r[2]);
                return null; //返回参数值
            },
            async checkNet() {
                let _this = this;
                try {
                    provider = new ethers.providers.Web3Provider(web3.currentProvider);
                    signer = provider.getSigner();
                    _this.checkLogin()
                } catch (e) {
                    _this.$toast(this.lang_list.home_warn1);
                }

            },
            async checkLogin() {
                let _this = this;
                setInterval(function () {
                    window.ethereum.enable()
                        .then(function (accounts) {
                            if (accounts.length == 0 && defaultAccount != "") {
                                window.location.reload();
                                return;
                            }
                            if (accounts.length == 0) {
                                return;
                            }
                            if (accounts[0] != defaultAccount) {
                                defaultAccount = accounts[0];
                                window.location.reload();
                                console.log("account change, start bat updateData!")
                            }
                        })
                        .catch(function (reason) {
                            // Handle error. Likely the user rejected the login:
                            console.log(reason === "User rejected provider access")
                        })
                }, 1000);
                window.ethereum.enable()
                    .then(function (accounts) {
                        if (accounts.length == 0) {
                            console.log('33333');
                            return;
                        }
                        defaultAccount = accounts[0];
                        console.log("defaultAccount: ", defaultAccount);
                        networkVersion = window.ethereum.networkVersion
                        if (networkVersion == 1) {  //以太坊主网
                            _this.type = 'ETH';
                            _this.$toast(this.lang_list.home_warn1);
                            return false;
                        } else if (networkVersion == 42) {   //以太坊测试网  K
                            _this.type = 'ETH';
                            _this.$toast(this.lang_list.home_warn1);
                            return false;
                        } else if (networkVersion == 128) {  //HECO主网
                            _this.type = 'HT';
                            _this.$toast(this.lang_list.home_warn1);
                            return false;
                        } else if (networkVersion == 256) { //HECO测试网
                            _this.type = 'HT';
                            _this.$toast(this.lang_list.home_warn1);
                            return false;
                        } else if (networkVersion == 56) { //币安主网
                            _this.type = 'BNB';
                        } else {
                            _this.$toast(this.lang_list.home_warn1);
                            return false;
                        }
                        usdtContract = new ethers.Contract(usdtAddress, usdtAbi, signer);
                        conContract = new ethers.Contract(conAddress, conAbi, signer);
                        if (defaultAccount != '') {
                            _this.address = defaultAccount;
                        }
                    });
                setTimeout(function () {
                    if (defaultAccount) {
                        _this.address = defaultAccount;
                        _this.short_address = _this.address.substr(0, 3) + '...' + _this.address.substr(_this.address.length - 3)
                    }

                }, 500)
            },
            async getLpBanner() {  //获取余额
                let _this = this;
                await lpContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.lpbanner = Math.floor(parseFloat(ethers.utils.formatEther(res)) * 100) / 100;
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
            toGong() {
                this.$router.push({
                    path: '/gong',
                    query: {}
                })
            },
        }
    }
</script>


